import React from 'react';
import PropTypes from 'prop-types';

const isNull = (prop) => {
  if (!prop) {
    return '0';
  }
  return prop;
};

const current = (total, next) => {
  if (total && next) {
    return isNull(next);
  }
  if (total) {
    return total;
  }
  return isNull(next);
};

const DisplayPanel = (props) => {
  const { total, next, modeSwitch } = props;
  let result = current(total, next);

  if (!modeSwitch) {
    if (result.length > 16) {
      let index = result.indexOf('e');
      let expString = result.substring(index, result.length);
      result = result.substring(0, 16 - expString.length) + expString;
    }
  }

  return (
    <div>
      <span type="text" className="overflow-auto text-right bg-gray-100 text-black text-2xl w-full h-18 px-2 flex justify-end items-end border-white border-2 border-gray-200 border-t-0 select-all" >
        {result}
      </span>
    </div>
  )
};

DisplayPanel.defaultProps = {
  total: null,
  next: null,
  modeSwitch: false
};

DisplayPanel.propTypes = {
  total: PropTypes.string,
  next: PropTypes.string,
  modeSwitch: PropTypes.bool
};

export default DisplayPanel;